import http from '@/api/http.js'
export default{
	data(){
		return{
			myTeam:[]
		}
	},
	// onNavigationBarButtonTap(e){
	// 	uni.navigateTo({  
	// 	  url: '/pages/viper/goodsExtend'  
	// 	});  
	// },
	onLoad(){
		this.getMyTeam()
	},
	methods:{
		getMyTeam(){
			let data = {
					token:uni.getStorageSync('token')
				}
			http('/api/user/customer','post',data).then(res=>{
				if(res.data.status == '200'){
					this.myTeam = res.data.data
				}
				console.log(res)
			})
		},
		toSonTeam(id,nickname){
			uni.navigateTo({
				url:'/pages/viper/sonTeam?otherId=' + id+'&nickname='+nickname
			})
		}
	}
}
  
